var sweeterMethods = {

    setActiveCoffee: function(index){
        this.activeCoffee = index
    },
    clickCoffee: function(e){
        var clickedIndex = event.currentTarget.dataset.id;
        this.setActiveCoffee(clickedIndex);
    },

    movePocket: debounce(function(index){
        var list = document.getElementsByClassName('list-item'),
            moveIt = list[index],
            landingArea = document.getElementById('landingArea'),
            startCoords = moveIt.getBoundingClientRect(),
            landingCoords = landingArea.getBoundingClientRect(),
            shiftX,
            shiftY,
            _this = this;

            this.pocketsBack();

            shiftX = landingCoords.left - startCoords.left;
            shiftY = landingCoords.top - startCoords.top;



            setTimeout(function() {
                _this.pocketsBack();
                moveIt.style.transform = 'scale(1,1) translate3d(' + shiftX + 'px,' + shiftY + 'px,0)'
            }, 500)

    }, 100),
    pocketsBack: function () {
        var list = document.getElementsByClassName('list-item');
        for(var i = 0; i < list.length; i++) {
            list[i].style.transform = '';
        }
    }

};


function debounce(func, wait, immediate) {
    var timeout;
    return function() {
        var context = this, args = arguments;
        var later = function() {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };
        var callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
    };
};